import React from 'react'
import { CCard, CCardHeader, CCardBody ,CSpinner} from '@coreui/react'
import axios from 'axios'


const Typography = () => {

  const [isloading,setIsLoading]=React.useState(false)
  const [pageData,setPageData]=React.useState({premium:0,business:0,users:[]})
  const getUsers= (values) => {

    setIsLoading(true);
  // console.log(process.env.REACT_APP_API_URL)
    axios
      .get(`http://127.0.0.1:8080/users/getUsers`)
      .then((res) => {
        setIsLoading(false);
        console.log(res.data);
        setPageData({...pageData,users:res.data.userData})
      
      })
      .catch((err) => {
        setIsLoading(false);
        console.log(err)
      
      });
  };
  const mapUsers=()=>{
    return pageData.users.length>0&&pageData.users.map((items,index)=><tr key={index}>
<td>
                  <p>
                    <code className="highlighter-rouge">{items._id}</code>
                  </p>
                </td>
                <td>
                <span className="h5">{items.Email}</span>
                </td>
                <td>
                <span className="h5">{items.plans}</span>
                </td>
    </tr>)
  }

  React.useEffect(()=>{
    getUsers()
  },[])
  return (
    <>
      <CCard className="mb-4">
        <CCardHeader>
          Registered User
      
        </CCardHeader>
        <CCardBody>
          <p>
       List Show all registered uers on the platform
          </p>
          <table className="table">
            <thead>
              <tr>
                <th>User Id</th>
                <th>Email</th>
                <th>Plan Package</th>
              </tr>
            </thead>
            <tbody>
           
             {isloading? <CSpinner color="primary" className='mx-auto' />:mapUsers()}
            </tbody>
          </table>
        </CCardBody>
      </CCard>
   
    </>
  )
}

export default Typography
